<template>
  <div class='moreCommentScroll'>
    <div class='scrollCon'>
      <div class="commentlist">          
          <div class="headImg" :style="{backgroundImage:'url('+Lists.headimg+')'}"></div>
          <div class="listCon">
              <div @click='entry(Lists.commentId,Lists.userId,{type:"comment",id:Lists.commentId})'>
                <div class="name" v-text="Lists.nickname"></div>
                <div class="text" v-text="Lists.comment"></div>
              </div>                                                              
          </div>
      </div>
      <hr />
      <div class="commentlist" v-if='Lists.replyNum' v-for="list in Lists.replyList" :key='list.name' >          
          <div class="headImg" :style="{backgroundImage:'url('+list.headimg+')'}"></div>
          <div class="listCon">                               
              <div >
                  <div class="otherList" @click='entry(Lists.commentId,list.userId,{type:"reply",id:list.commentId})'>
                      <div class="name" v-text="nickname(list)"></div>
                      <div class="text" v-text="list.comment"></div>
                  </div>
              </div>                    
          </div>
      </div>
      <v-loadmove v-if='Lists.replyList&&Lists.replyList.length==0' type='more'/>
    </div>
  </div>
</mt-popup>      
</template>
<script>
  import loadMove1 from '../../components/seeOcean/loadMore';
	export default {
		name: '',
		components: {
      'v-loadmove': loadMove1,
		},
		props: ['entry','Lists'],
		data() {
			return {}
		},
    methods:{
      nickname(data){
        if(!data.atUserId||(data.atUserId&&data.atUserId==data.userId)){
          return `| ${data.nickname}`;
        }else{
          return `${data.nickname} 回复 ${data.atNickname}`;
        }
      },
    }
	}
</script>
<style lang="scss" scoped>
	* {
		border: none;
		margin: 0;
		padding: 0;
	}
		hr {
			height: 0.2rem;
			background-color: #f2f2f2;
      margin-bottom: 0.36rem;
		}
	.commentlist{
        width: 6.9rem;
        margin: 0.36rem 0.3rem;
        display: flex;
        .headImg{
            width: 0.72rem;
            height: 0.72rem;
            background-size: 0.72rem 0.72rem;
            background-repeat: no-repeat;
            border-radius: 0.72rem; 
            margin-right: 0.24rem
        }
        .listCon{
            overflow: hidden;
            flex: 1;
            .name{
                font-size: 0.28rem;
                color: #1797c6;
            }
            #replyTips{
                font-size: 0.28rem;
                color: #888888;
                margin-top: 0.11rem;
            }
            .text{
                word-break: break-all;
                word-wrap: break-word;
                line-height: 0.4rem;
                font-size: 0.32rem;
                color: #333333;
                // line-height: 0.58rem;
                margin: 0.11rem 0 0;
            }
            .otherList{
                margin: 0.11rem 0 0;
            }
        }
  }
    .moreCommentScroll{
      width:100%;
      height:100%;
      background:white;
      position: absolute;
      bottom: 0;
      left: 0;
    }
    .scrollCon{
      width: 100%;
      height: 90%;
      overflow: scroll;
      margin: 5% 0;
      -webkit-overflow-scrolling:touch;
      .commentlist{
        margin:0 0.3rem 0.36rem 0.3rem;
      }
    }
</style>



