<template>
    <div class="commentlistsCon">
        <div class="commentHeader">评论</div>
        <div v-for="(item,index) in CommentList" :key='item.id'>
            <div class="commentlist">          
                <div class="headImg" :style="{backgroundImage:'url('+item.headimg+')'}"></div>
                <div class="listCon">
                    <div @click='entry(item.commentId,item.userId,{type:"comment",id:item.commentId})'>
                      <div class="name" v-text="item.nickname"></div>
                      <div class="text" v-text="item.comment"></div>
                    </div>
                    <div v-if='item.replyNum'>
                        <div class="otherList" v-for="list in item.replyList" :key='list.name' @click='entry(item.commentId,list.userId,{type:"reply",id:list.commentId})'>
                            <div class="name" v-text="`| ${list.nickname}`"></div>
                            <div class="text" v-text="list.comment"></div>
                        </div>
                    </div>
                    <div id='replyTips' v-if='item.replyNum>2' @click='getReplyList(item)'>查看全部{{item.replyNum}}条回复></div>
                </div>
            </div>
            <hr v-if='index!=CommentList.length-1'/>
        </div>
        <div class='CommentTips' @click='entry()' v-if='CommentList.length==0'>暂时没有评论,点击添加你的评论吧^_^</div>
    </div>
</template>
<script>
import loadMove1 from '../../components/seeOcean/loadMore';

export default {
    name:'',
    props:['CommentList','entry','getReplyList'],
    components: {
      'v-loadmove': loadMove1
		},
    data () {
        return {
        }
    },
    computed:{
    },
    created() {
    },
    methods:{
    }
}
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
.commentlistsCon{
    border-bottom: 0.01rem solid rgb(229,229,229); 
    .commentHeader{
        width: 6.9rem;
        height: 0.96rem;
        line-height: 0.96rem;
        font-size: 0.32rem;
        text-align: center;
        color: #555555;
        margin-left: 0.33rem;
        border-bottom: 0.01rem solid rgb(229,229,229);
    }
    hr{
        width: 6.9rem;
        margin: 0.36rem 0.3rem;
        height: 1px;
        background: #f2f2f2;
    }
    .commentlist{
        width: 6.9rem;
        margin: 0.36rem 0.3rem;
        display: flex;
        .headImg{
            width: 0.72rem;
            height: 0.72rem;
            background-size: 0.72rem 0.72rem;
            background-repeat: no-repeat;
            border-radius: 0.72rem; 
            margin-right: 0.24rem
        }
        .listCon{
            overflow: hidden;
            flex: 1;
            .name{
                font-size: 0.28rem;
                color: #1797c6;
            }
            #replyTips{
                font-size: 0.28rem;
                color: #888888;
                margin-top: 0.11rem;
            }
            .text{
                word-break: break-all;
                word-wrap: break-word;
                line-height: 0.4rem;
                font-size: 0.32rem;
                color: #333333;
                // line-height: 0.58rem;
                margin: 0.11rem 0 0;
            }
            .otherList{
                margin: 0.11rem 0 0;
            }
        }
    }
    .CommentTips{
      padding:0.3rem 0;
      font-size: 0.2rem;
      text-align:center;
    }
    .popup{
      width: 100%;
      height:50%;
      background:rgba(0,0,0,0);
    }
    .moreCommentScroll{
      width:100%;
      height:100%;
      background:white;
      position: absolute;
      bottom: 0;
      left: 0;
    }
    .scrollCon{
      width: 100%;
      height: 90%;
      overflow: scroll;
      margin: 5% 0;
      -webkit-overflow-scrolling:touch;
      .commentlist{
        margin:0 0.3rem 0.36rem 0.3rem;
      }
    }
}
 .fadebottom-enter-active,
  .fadebottom-leave-active {
    transform: translate3d(0, 0, 0);
    transition: transform 0.5s, opacity 0.5s;
  }

  .fadebottom-enter,
  .fadebottom-leave-to {
    transform: translate3d(0, 100%, 0);
    transition: transform 0.5s, opacity 0.5s;
  }
</style>




